.news {
  &.news--recommended {
    @media (min-width: $medium) {
      padding-bottom: 15px;
    }

    @media (min-width: $extra-large) {
      padding-bottom: 31px;
    }
  }
}

.news__slider {
  /*margin-left: -10px;
  margin-right: -10px;*/
}

.news__wrapper {
  padding-bottom: 58px;
  padding-top: 40px;

  &--home {
    padding-top: 0;
    padding-bottom: 31px;

    @media (min-width: $extra-large) {
      padding-bottom: 40px;
    }
  }
}

.news__inner {
  .news--widget & {
    margin-right: 10px;
    margin-left: 10px;

    @media (min-width: $extra-large) {
      margin-right: 5px;
      margin-left: 5px;
    }
  }
}

.news__title {
  padding-top: 5px;
  padding-bottom: 3px;
  font-size: 20px;
  line-height: 23px;

  @media (min-width: $medium) {
    font-size: 25px;
    line-height: 31px;
  }

  @media (min-width: $extra-large) {
    font-size: 35px;
    line-height: 41px;
  }
}

.news__alt-title {
  margin-bottom: 0;
  font-size: 25px;
  line-height: 31px;
  color: #CCA643;

  @media (min-width: $extra-large) {
    margin-top: 22px;
    font-size: 35px;
    line-height: 41px;
  }
}
