.logo {
  @media (min-width: $medium) {
    max-width: 160px;
  }

  @media (min-width: $extra-large) {
    max-width: 100%;
  }

  &--page-footer {
    display: block;
    margin: 0 auto 17px auto;

    @media (min-width: $extra-large) {
      margin-bottom: 15px;
    }
  }
}

.logo__wrapper {
  display: block;
  margin-top: 15px;

  @media (min-width: $extra-large) {
    margin-top: 30px;
  }
}
