.navigation {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 20;
  padding-bottom: 42px;
  background: rgba(12, 14, 32, 0.9);

  @media (min-width: $medium) {
    position: initial;
    display: block;
    padding: 10px 15px;
  }

  @media (min-width: $extra-large) {
    background: rgba(12, 14, 32, 1);
  }

  &.open {
    display: block;
  }
}

.navigation__top {
  position: relative;
  background-color: #000000;
  padding-bottom: 6px;

  @media (min-width: $medium) {
    display: none;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: -17px;
    left: 50%;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 17px 36.5px 0 36.5px;
    border-color: #000000 transparent transparent transparent;
    transform: translateX(-36.5px);
  }
}

.navigation__logo {
  display: block;
  margin: 0 auto;
}

.navigation__close {
  position: absolute;
  top: 15px;
  left: 15px;
  background-color: transparent;
  padding-right: 0;
  padding-left: 0;
  border: 0;
}

.navigation__close-wrapper {
  position: relative;
  width: 28px;
  height: 28px;
  transform: rotate(-45deg);

  @media (min-width: $medium) {
    display: none;
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    background: linear-gradient(180deg, #A26E1A 14.06%, #FFFFCC 47.4%, #AB611E 77.6%);
  }

  &::before {
    left: 0;
    top: 13px;
    width: 100%;
    height: 2px;
  }

  &::after {
    left: 13px;
    top: 0;
    width: 2px;
    height: 100%;
  }
}
