.page-footer {
  padding-top: 8px;
  padding-bottom: 7px;
  background-color: #0A0C17;

  @media (min-width: $extra-large) {
    position: relative;
    padding-top: 20px;
    padding-bottom: 18px;
  }
}

.page-footer__grid {
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (min-width: $medium) {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
  }

  @media (min-width: $extra-large) {
    justify-content: flex-start;
  }
}

.page-footer__item {
  @media (min-width: $medium) {
    &--portfolio {
      width: 100%;
    }
  }

  @media (min-width: $extra-large) {
    &--phones {
      order: -2;
      margin-left: 55px;
    }

    &--menu {
      order: -1;
      margin-left: 129px;
    }

    &--address {
      order: 0;
      margin-left: 118px;
    }

    &--portfolio {
      order: 1;
      width: auto;
      margin-left: auto;
      margin-right: 57px;
    }
  }
}

.page-footer__item-title {
  margin-top: 5px;
  margin-bottom: 8px;
  line-height: 19px;
  text-transform: uppercase;
  color: #FFFFFF;
  text-align: center;

  @media (min-width: $extra-large) {
    margin-bottom: 21px;
    text-align: left;

    .page-footer__item--portfolio & {
      text-align: right;
    }
  }

  & span {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      top: 7px;
      right: -15px;
      width: 6px;
      height: 6px;
      background-image: url("../../img/arrow-bottom.svg");
      background-position: center;
      background-repeat: no-repeat;

      @media (min-width: $medium) {
        display: none;
      }
    }
  }

  &.active {
    & span::after {
      top: 9px;
      right: -12px;
      transform: rotate(180deg);
    }

    & + .page-footer__content {
      display: block;
    }
  }
}

.page-footer__content {
  display: none;

  @media (min-width: $medium) {
    display: block;
  }

  & > p {
    margin-top: 0;
    margin-bottom: 5px;
    font-size: 14px;
    line-height: 16px;

    @media (min-width: $extra-large) {
      margin-bottom: 6px;
      font-size: 13px;
      line-height: 15px;
    }
  }

  & a {
    color: inherit;
    text-decoration: none;
  }
}

.page-footer__copyright {
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  color: #FFFFFF;

  @media (min-width: $extra-large) {
    max-width: 330px;
    margin: 8px auto 0 auto;
  }
}
