.page-header__inner {
  background-image: url("../../img/bg-header.png");

  @media (min-width: $medium) {
    background-image: url("../../img/bg-header--tab.png");
  }

  @media (min-width: $extra-large) {
    padding-bottom: 23px;
  }
}

.page-header__content {
  display: flex;
  justify-content: space-between;

  @media (min-width: $medium) {
    align-items: center;
  }
}

.page-header__contacts {
  margin-top: 8px;
  margin-right: -6px;
  text-align: right;

  @media (min-width: $medium) {
    display: flex;
    align-items: flex-start;
    margin-right: 0;
  }

  @media (min-width: $extra-large) {
    margin-top: 70px;
    margin-right: 12px;
  }
}

.page-header__contacts-mail {
  display: inline-block;
  font-size: 14px;
  line-height: 16px;
  text-decoration: none;

  @media (min-width: $medium) {
    position: relative;
    padding-left: 30px;
    font-size: 16px;
    line-height: 19px;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 20px;
      height: 20px;
      background-image: url("../../img/ico-mail.svg");
      background-repeat: no-repeat;
    }
  }
}

.page-header__contacts-address {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 12px;
  line-height: 14px;

  @media (min-width: $medium) {
    position: relative;
    order: -2;
    max-width: 150px;
    margin-right: 20px;
    padding-left: 30px;
    font-size: 16px;
    line-height: 19px;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 20px;
      height: 20px;
      background-image: url("../../img/ico-place.svg");
      background-repeat: no-repeat;
    }
  }

  @media (min-width: $extra-large) {
    margin-right: 45px;
    text-align: left;
    margin-top: 2px;
  }
}

.page-header__bottom {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding-bottom: 9px;

  @media (min-width: $medium) {
    display: none;
  }
}
