.product-gallery {
  @media (min-width: $medium) {
    width: calc(50% - 10px);
  }

  @media (min-width: $extra-large) {
    width: calc(59.3% - 10px);
    max-width: 475px;
    margin-right: 20px;
  }

  & img {
    display: block;
    width: 100%;
    border-radius: 5px;
  }
}

.product-gallery__thumbnail {
  border-radius: 3px;
  overflow: hidden;

  @include List-Reset();

  .product-gallery--catalog & {
    border: 2px solid rgba(59, 59, 59, 0.2);
  }
}

.product-gallery__preview {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  padding-left: 10px;

  @include List-Reset(10px, 0, 0);

  @media (min-width: $extra-large) {
    margin-top: 5px;
  }
}

.product-gallery__preview > li {
  width: 80px;
  margin-left: 5px;
  margin-right: 5px;
  box-sizing: border-box;
  border: 2px solid transparent;
  border-radius: 5px;

  @media (min-width: $extra-large) {
    height: 80px;
    overflow: hidden;

    & > img {
      height: 80px;
      width: 80px;
      object-fit: cover;
    }
  }

  &:hover {
    border-color: #A26E1A;
  }

  &.slick-current {
    border-color: #A26E1A;
  }
}
