.direction-item {
  color: #ffffff;

  @media (min-width: $medium) {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &:nth-child(2n) {
      flex-direction: row-reverse;
    }
  }

  &:not(:last-child) {
    margin-bottom: 30px;

    @media (min-width: $extra-large) {
      margin-bottom: 40px;
    }
  }
}

.direction-item__thumbnail {
  @media (min-width: $medium) {
    width: calc(50% - 10px);
  }

  & img {
    width: 100%;
    display: block;
    border-radius: 5px;
  }
}

.direction-item__content {
  padding-top: 14px;

  @media (min-width: $medium) {
    width: calc(50% - 10px);
    padding-top: 0;
  }
}

.direction-item__title {
  margin-bottom: 22px;
  line-height: 18px;
  font-weight: 400;
  font-size: 16px;

  @media (min-width: $medium) {
    margin-top: 0;
  }

  @media (min-width: $extra-large) {
    margin-right: -15px;
    font-size: 30px;
    line-height: 34px;
  }
}

.direction-item__content {
  & p {
    font-size: 14px;
    line-height: 16px;
    margin-bottom: 28px;

    @media (min-width: $extra-large) {
      font-size: 21px;
      line-height: 24px;
    }
  }
}
