.catalog {
  display: flex;
  flex-wrap: wrap;
  @include List-Reset(0, 21px, 0);
  margin-right: -10px;
  margin-left: -10px;
}

.catalog__grid {
  @media (min-width: $extra-large) {
    display: flex;
    align-items: flex-start;
    margin-top: 40px;
  }
}

.catalog__detail {
  @media (min-width: $extra-large) {
    width: calc(75% - 20px);
  }
}

.catalog__content {
  padding-top: 31px;
  padding-bottom: 16px;

  @media (min-width: $extra-large) {
    display: none;
  }
}

.catalog__title {
  margin-top: 0;
  margin-bottom: 30px;
  text-align: center;
  font-size: 20px;
  line-height: 23px;
  font-weight: 500;
  color: #E8BE51;
}


.catalog__description {
  font-size: 18px;
  line-height: 21px;
}
