.main-content {
  padding-bottom: 40px;

  .page-main--disigners & {
    padding-bottom: 5px;
  }

  & > h1 {
    margin-top: 0;
    font-size: 25px;
    line-height: 31px;
    text-align: center;
    font-weight: 400;
    color: #CCA643;

    @media (min-width: $extra-large) {
      margin-bottom: 32px;
      font-size: 35px;
      line-height: 41px;
    }
  }

  & p {
    font-size: 18px;
    line-height: 21px;

    @media (min-width: $extra-large) {
      font-size: 21px;
      line-height: 25px;
    }

    &.color {
      margin-bottom: 0;
      color: #CCA643;
      text-transform: uppercase;

      & + p {
        margin-top: 0;
      }
    }

    &.color-bold {
      margin-bottom: 0;
      color: #CCA643;
      font-weight: 700;
      font-style: italic;
    }
  }

  &--brands {
    & > div {
      @media (min-width: $medium) {
        overflow: hidden;
      }
    }

    & p {
      &.color {
        margin-bottom: 9px;
        margin-top: 0;
      }
    }

    & img {
      @media (min-width: $medium) {
        float: right;
        max-width: 50%;
        margin-left: 20px;
        margin-bottom: 20px;
      }

      @media (min-width: $extra-large) {
        max-width: 100%;
      }
    }
  }
}

.main-content__wrapper {
  padding-top: 30px;

  @media (min-width: $extra-large) {
    padding-top: 40px;
  }

  &--darken {
    background: rgba(0, 0, 0, 0.4);
  }
}
