.advantages {
  @include List-Reset();
  display: flex;
  flex-direction: column;

  @media (min-width: $medium) {
    flex-direction: row;
  }

  @media (min-width: $extra-large) {
    max-width: 80%;
    margin: 0 auto;
  }
}

.advantages__wrapper {
  padding-top: 19px;
  background: rgba(0, 0, 0, 0.5);

  @media (min-width: $extra-large) {
    padding-top: 32px;
  }
}

.advantages__item {
  margin-bottom: 44px;
  text-align: center;

  @media (min-width: $medium) {
    width: 25%;
  }

  @media (min-width: $extra-large) {
    margin-bottom: 32px;
  }
}

.advantages__icon {
  fill: #FFFFFF;
}

.advantages__detail {
  margin-top: 0px;
  margin-bottom: 0;
  font-size: 40px;
  line-height: 47px;
  font-weight: 700;
  color: #A26E1A;
}

.advantages__description {
  margin-top: 5px;
  margin-bottom: 0;
  font-size: 13px;
  line-height: 15px;
  color: #FFFFFF;
}
