.search {
  position: relative;
  width: 135px;
  margin-top: 4px;

  &::after {
    content: "";
    position: absolute;
    right: 5px;
    top: 5px;
    width: 15px;
    height: 15px;
    background-image: url("../../img/magnifier.svg");
    background-position: center;
    background-repeat: no-repeat;
  }
}

.search__text {
  width: 100%;
  padding: 4px 20px 6px 5px;
  background-color: #0c0e20;
  border-radius: 5px;
  border: none;
  box-sizing: border-box;

  &::placeholder {
    font-family: inherit;
    font-size: 14px;
    line-height: 16px;
    color: rgba(255, 255, 255, 0.5);
  }
}
