.contacts {
  color: #FFFFFF;

  @media (min-width: $medium) {
    display: flex;
    justify-content: space-between;
  }

  @media (min-width: $extra-large) {
    max-width: 78%;
    margin: 0 auto;
  }

  & > p {
    font-size: 14px;
    line-height: 16px;
  }
}

.contacts__wrapper {
  padding-top: 29px;
  padding-bottom: 28px;

  @media (min-width: $extra-large) {
    padding-top: 38px;
    padding-bottom: 40px;
  }
}

.contacts__title {
  padding-bottom: 1px;
  margin-bottom: 13px;
  padding-top: 2px;
  font-style: normal;

  @media (min-width: $extra-large) {
    padding-bottom: 7px;
    margin-bottom: 40px;
  }
}

.contacts__phones {
  padding-top: 10px;

  @media (min-width: $medium) {
    width: calc(50% - 10px);
    padding-top: 0;
  }

  @media (min-width: $extra-large) {
    width: calc(39% - 10px);
    font-size: 21px;
    line-height: 101.27%;
  }
}

.contacts__phones-title {
  font-size: 18px;
  line-height: 101.27%;

  @media (min-width: $medium) {
    margin-top: 0;
  }

  @media (min-width: $extra-large) {
    font-size: 21px;

    & + p {
      font-size: 21px;
      line-height: 101.27%;
    }
  }
}
