body {
  min-width: $small;
  @include Roboto();
  font-size: 16px;
  line-height: 1.5;
  color: #ffffff;
  background-color: #3a445d;
  background-image: url("../img/main-bg.jpg");
  background-size: 683px auto;
}

img {
  max-width: 100%;
  height: auto;
}

// Вставка, добавляющая градиентные рамки вверху и внизу блока @extend %extend-name;
%gradient-border {
  position: relative;

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 50%;
    width: 65px;
    height: 1px;
    transform: translateX(-32.5px);
    background: linear-gradient(90deg, #A26E1A 1.83%, #FFFFCC 52.41%, #AB611E 98.54%);
  }

  &::before {
    top: 0;
  }

  &::after {
    bottom: 0;
  }
}
