.phones-list {
  @include List-Reset();
}

.phones-list__item {
  line-height: 101.27%;

  @media (min-width: $extra-large) {
    font-size: 21px;
  }

  .phones-list--contacts-page {
    margin-bottom: 20px;

    & {
      line-height: 1;
    }
  }

  & > a {
    text-decoration: none;
    color: #FFFFFF;
  }
}

.phones-list--footer {
  & .phones-list__item {
    margin-bottom: 5px;
    font-size: 14px;
    line-height: 16px;

    @media (min-width: $extra-large) {
      margin-bottom: 6px;
      font-size: 13px;
      line-height: 15px;
    }
  }
}
