.single-news {
  padding-top: 30px;

  @media (min-width: $extra-large) {
    padding-top: 40px;
  }
}

.single-news__grid {
  @media (min-width: $medium) {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
}

.single-news__thumbnail {
  display: block;
  border-radius: 5px;
  width: 100%;
}

.single-news__thumbnail-wrapper {
  box-shadow: inset 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  overflow: hidden;

  @media (min-width: $medium) {
    width: calc(50% - 10px);
  }
}

.single-news__content {
  padding-bottom: 0;

  @media (min-width: $medium) {
    width: calc(50% - 10px);
  }

  & > h1 {
    text-align: left;
    margin-top: 1em;

    @media (min-width: $medium) {
      margin-top: 0;
    }

    @media (min-width: $extra-large) {
      margin-bottom: 10px;
      font-size: 35px;
      line-height: 41px;

      & + p {
        margin-top: 0;
      }
    }
  }
}
