.map {
  height: 200px;
  background-image: url("../../img/map.jpg");

  @media (min-width: $medium) {
    height: 400px;
  }

  @media (min-width: $extra-large) {
    height: 683px;
    margin-bottom: 40px;
  }
}

.map__wrapper {
  margin-top: 30px;
  box-shadow: inset 0px 4px 10px rgba(0, 0, 0, 0.25);
  overflow: hidden;
  border-radius: 5px;

  @media (min-width: $extra-large) {
    margin-top: 40px;
  }
}


