.news-item__wrapper {
  margin-right: -15px;
  margin-left: -15px;

  .news--widget & {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    padding-right: 0;
    padding-left: 0;
  }

  .news--catalog & {
    padding: 30px 15px 10px 15px;
  }

  .news--catalog:not(.news--recommended) & {
    @media (min-width: $medium) {
      display: flex;
      flex-wrap: wrap;
      margin-right: -10px;
      margin-left: -10px;
      padding-right: 0;
      padding-left: 0;
    }

    @media (min-width: $extra-large) {
      padding-top: 40px;
    }
  }

  .news--catalog.news--recommended & {
    @media (min-width: $medium) {
      margin-right: -10px;
      margin-left: -10px;
      padding-right: 0;
      padding-left: 0;
    }
  }

  .news--darken & {
    background-color: rgba(0, 0, 0, 0.4);

    @media (min-width: $extra-large) {
      background-color: transparent;
    }
  }
}

.news-item {
  .news--widget & {
    width: calc(50% - 20px);
    margin-right: 10px;
    margin-bottom: 10px;
    margin-left: 10px;

    @media (min-width: $medium) {
      position: relative;
      overflow: hidden;

      &:nth-child(1) {
        width: calc(40% - 20px);

        @media (min-width: $extra-large) {
          width: calc(41.6% - 20px);
        }
      }

      &:nth-child(2),
      &:nth-child(3) {
        width: calc(30% - 20px);
        height: calc(60% - 20px);
        overflow: hidden;

        @media (min-width: $extra-large) {
          width: calc(29.2% - 20px);
          height: calc(53% - 20px);
        }
      }

      &:nth-child(4) {
        position: absolute;
        width: calc(60% - 20px);
        margin-left: calc(40% + 10px);
        height: calc(40% - 10px);
        overflow: hidden;
        bottom: 0;

        @media (min-width: $extra-large) {
          width: calc(58.3% - 20px);
          margin-left: calc(41.7% + 10px);
          height: calc(47% - 10px);
        }
      }
    }
  }

  .news--catalog:not(.news--recommended) & {
    @media (min-width: $medium) {
      width: calc(50% - 20px);
      margin-left: 10px;
      margin-right: 10px;
      margin-bottom: 19px;
    }

    @media (min-width: $extra-large) {
      width: calc((100% / 3) - 20px);
    }
  }

  .news--catalog & {
    @media (min-width: $medium) {
      margin-left: 10px;
      margin-right: 10px;
    }
  }
}

.news-item__thumbnail {
  display: block;
  width: 100%;
  border-radius: 5px 5px 0 0;

  @media (min-width: $medium) {
    .news--widget & {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }
}

.news-item__thumbnail-wrapper {
  position: relative;

  @media (min-width: $medium) {
    .news--widget & {
      height: 100%;

      & > picture {
        height: 100%;
      }
    }
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0.92%, rgba(0, 0, 0, 0.6) 83.08%);
    opacity: 0.6;

    .news--catalog & {
      box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
  }
}

.news-item__detail {
  @media (min-width: $medium) {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 20px;
    display: flex;
    flex-direction: column-reverse;
    text-align: right;
  }

  @media (min-width: $extra-large) {
    align-items: flex-end;
  }
}

.news-item__published {
  margin-top: 10px;
  margin-bottom: 0;
  font-size: 14px;
  line-height: 16px;
  color: #FFFFFF;

  @media (min-width: $extra-large) {
    font-size: 16px;
    line-height: 19px;

    .news--widget & {
      margin-top: 0;
    }
  }

  .news--catalog & {
    margin-top: 6px;

    @media (min-width: $extra-large) {
      margin-top: 12px;
      font-size: 18px;
      line-height: 21px;
    }
  }
}

.news-item__title {
  margin-top: 5px;
  margin-bottom: 0;
  line-height: 19px;
  font-weight: 700;
  color: #CCA643;

  @media (min-width: $extra-large) {
    font-size: 18px;
    line-height: 21px;

    .news--widget & {
      max-width: 347px;
    }
  }

  .news--catalog & {
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;

    @media (min-width: $extra-large) {
      font-size: 21px;
      line-height: 25px;
    }
  }

  .news--recommended & {
    font-weight: 400;
  }

  & > a {
    color: inherit;
    text-decoration: none;
  }
}

.news-item__more {
  position: relative;
  display: inline-block;
  padding-bottom: 1px;
  margin-top: 15px;
  line-height: 19px;
  color: #FFFFFF;
  text-decoration: none;

  @media (min-width: $extra-large) {
    margin-top: 10px;
    font-size: 18px;
    line-height: 21px;
  }

  &::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    height: 1px;
    background: linear-gradient(90deg, #A26E1A 1.83%, #FFFFCC 52.41%, #AB611E 98.54%);
  }

  &::after {
    bottom: 0;
  }
}
