.portfolio-item {
  position: relative;
  width: calc(50% - 20px);
  margin-right: 10px;
  margin-bottom: 10px;
  margin-left: 10px;

  @media (min-width: $medium) {
    width: calc(25% - 20px);
    margin-bottom: 20px;
  }
}

.portfolio-item__thumbnail {
  display: block;
  border-radius: 5px;
  width: 100%;
}

.portfolio-item__title {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
  align-items: flex-end;
  justify-content: center;
  margin-top: 0;
  margin-bottom: 0;
  padding-bottom: 7px;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  color: #CCA643;
  background: linear-gradient(180deg, rgba(14, 19, 31, 0) 75.7%, rgba(3, 7, 20, 0.5) 89.79%);

  @media (min-width: $extra-large) {
    padding-bottom: 12px;
    font-size: 18px;
    line-height: 24px;
  }

  .portfolio-item:hover & {
    display: flex;
  }
}
