.slick-dots {
  display: flex;
  justify-content: center;
  @include List-Reset(0, 0, 0);

  .news--widget & {
    margin-top: 26px;

    @media (min-width: $extra-large) {
      margin-top: 19px;
    }
  }

  .news--recommended & {
    margin-top: 26px;

    @media (min-width: $extra-large) {
      margin-top: 39px;
      justify-content: flex-start;
    }
  }

  & li {
    margin-right: 5px;
    margin-left: 5px;

    @media (min-width: $extra-large) {
      margin-right: 7px;
      margin-left: 7px;
    }
  }

  & button {
    display: block;
    width: 10px;
    height: 10px;
    padding-right: 0;
    padding-left: 0;
    font-size: 0;
    color: transparent;
    border-radius: 50%;
    border: none;
    background: linear-gradient(90deg, #A26E1A 1.83%, #FFFFCC 52.41%, #AB611E 98.54%);

    @media (min-width: $extra-large) {
      width: 15px;
      height: 15px;
    }
  }

  & .slick-active button {
    position: relative;

    &::before {
      content: "";
      width: 8px;
      height: 8px;
      position: absolute;
      left: 1px;
      top: 1px;
      background-color: #0a0c17;
      border-radius: 50%;

      @media (min-width: $extra-large) {
        width: 13px;
        height: 13px;
      }
    }
  }
}
