.capcha {
  margin-bottom: 0;
  margin-left: 20px;
  margin-right: 10px;
  width: calc(100% - 210px);

  .contacts-page--custom & {
    @media (min-width: $extra-large) {
      max-width: 140px;
    }
  }
}

.capcha__wrapper {
  display: flex;
  margin-top: 3px;
  margin-bottom: 10px;
}

.capcha__test {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 9px;
  flex-shrink: 0;
  width: 139px;
  font-size: 16px;
  line-height: 19px;
  color: #373636;
  border-radius: 5px;
  background-color: #ffffff;
}

.capcha__reload {
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  height: 30px;
  width: 30px;
  border: 0;
  border-radius: 5px;
  background-color: #ffffff;
}
