.promo__item {
  position: relative;

  @media (min-width: $extra-large) {
    & picture {
      width: 100%;
      display: block;
    }
  }
}

.promo__thumbnail {
  display: block;
  width: 100%;

  @media (min-width: $extra-large) {
    width: 1440px;
    height: 288px;
    object-fit: cover;
  }

  .promo--home & {
    @media (min-width: $extra-large) {
      height: 508px;
    }
  }
}

.promo__content {
  position: absolute;
  left: 0;
  right: 0;
  top: 23px;
  bottom: 28px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: linear-gradient(180deg, rgba(10, 12, 23, 0.5) -59.17%, rgba(10, 12, 23, 0.23) 151.67%);

  @media (min-width: $medium) {
    top: 30%;
    bottom: 30%;
  }

  @media (min-width: $extra-large) {
    height: 60px;
    top: calc(50% - 30px);
  }

  .promo--home & {
    top: 16px;
    bottom: 16px;

    @media (min-width: $medium) {
      top: 30%;
      bottom: 30%;
    }

    @media (min-width: $extra-large) {
      top: 40%;
      bottom: 40%;
    }
  }

  & > p {
    max-width: 290px;
    margin-top: 0;
    margin-bottom: 0;
    font-size: 18px;
    line-height: 21px;
    color: #ffffff;
    @include Roboto(400, italic);

    @media (min-width: $medium) {
      max-width: 949px;
      font-size: 25px;
      line-height: 31px;
    }

    @media (min-width: $extra-large) {
      font-size: 35px;
      line-height: 41px;
    }

    .promo--home & {
      font-size: 16px;
      line-height: 19px;

      @media (min-width: $medium) {
        font-size: 25px;
        line-height: 31px;
      }

      @media (min-width: $extra-large) {
        font-size: 35px;
        line-height: 41px;
      }
    }
  }
}

.promo--home {
  & .img-responsive {
    display: none;
  }
}
