.pagination {
  display: flex;
  justify-content: center;
  @include List-Reset();
  padding-top: 30px;
  padding-bottom: 30px;

  @media (min-width: $extra-large) {
    padding-top: 0;
    justify-content: flex-start;
    margin-left: -5px;
    padding-bottom: 40px;
  }
}

.pagination__item {
  margin-right: 2.5px;
  margin-left: 2.5px;
  line-height: 16px;

  @media (min-width: $extra-large) {
    line-height: 19px;
  }

  & span,
  & a {
    display: inline-block;
    padding: 2px 4px;
    font-size: 14px;
    text-decoration: none;
    color: #FFFFFF;
    border-radius: 2px;
    background-color: #373737;

    @media (min-width: $extra-large) {
      font-size: 16px;
    }

    &.current {
      background-color: #CCA643;
    }
  }

  & svg {
    fill: #ffffff;
  }
}
