.menu-trigger {
  position: relative;
  display: flex;
  margin-top: 8px;
  padding-right: 0;
  padding-left: 0;
  width: 20px;
  height: 14px;
  border: 0;
  background-color: transparent;
}

.menu-trigger__line {
  width: 100%;
  height: 2px;
  margin-top: auto;
  margin-bottom: auto;
  background: linear-gradient(90deg, #A26E1A 1.83%, #FFFFCC 52.41%, #AB611E 98.54%);

  &:first-child,
  &:last-child {
    position: absolute;
    left: 0;
  }

  &:first-child {
    top: 0;
  }

  &:last-child {
    bottom: 0;
  }
}
