.menu-item-has-children {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    top: 9px;
    right: 0;
    width: 10px;
    height: 10px;
    background-image: url("../../img/arrow-right.svg");
    background-position: center;
    background-repeat: no-repeat;
  }

  &.open::after {
    background-image: url("../../img/arrow-bottom-active.svg");
  }

  & > a {
    color: #A26E1A;
  }
}
