.category {
  @include List-Reset();

  & > li {
    font-size: 18px;
    line-height: 21px;

    &:not(:last-child) {
      margin-bottom: 21px;
    }

    & > a {
      color: #FFFFFF;
    }
  }

  & a {
    text-decoration: none;
  }
}

.category__wrapper {
  padding: 29px 18px 23px 23px;
  margin-right: -15px;
  margin-left: -15px;
  background: rgba(0, 0, 0, 0.5);

  @media (min-width: $extra-large) {
    flex-shrink: 0;
    width: 25%;
    margin-right: 20px;
    padding: 30px 30px 40px 43px;
    border-radius: 5px;
    box-sizing: border-box;
  }
}
