.contacts-form {
  text-align: center;

  .contacts-page--custom & {
    @media (min-width: $extra-large) {
      display: flex;
      flex-wrap: wrap;
      align-items: flex-start;
    }
  }
}

.contacts-form__wrapper {
  @media (min-width: $medium) {
    &--home {
      width: calc(50% - 10px);

      @media (min-width: $extra-large) {
        width: calc(53% - 10px);

        & .contacts-form {
          text-align: left;
        }
      }
    }
  }

  & > p {
    font-size: 14px;
    line-height: 16px;
    margin-bottom: 18px;

    @media (min-width: $medium) {
      margin-top: 0;
    }

    @media (min-width: $extra-large) {
      margin-bottom: 21px;
      font-size: 18px;
      line-height: 21px;
    }

    .contacts-page__item--form & {
      margin-bottom: 28px;
    }
  }
}

.contacts-form__text {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  line-height: 26px;
  margin-bottom: 10px;
  text-align: left;
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  border-radius: 5px;
  background-color: transparent;

  &--textarea {
    height: 114px;
    resize: none;
  }

  &::placeholder {
    color: #ffffff;
    font-family: inherit;
    opacity: 1;
  }
}

.contacts-form__submit {
  margin-top: 19px;
  padding-right: 10px;
  padding-left: 10px;

  .contacts-page--custom & {
    @media (min-width: $extra-large) {
      margin-top: 3px;
      margin-left: auto;
    }
  }
}

