.phone-list {
  @include List-Reset();

  @media (min-width: $medium) {
    margin-right: 20px;
  }

  &--header {
    @media (min-width: $extra-large) {
      display: flex;
      flex-direction: column-reverse;
      align-items: flex-end;
      transform: translate(-11px, 5px);
    }
  }
}

.phone-list__item {
  line-height: 19px;

  @media (min-width: $extra-large) {
    margin-bottom: 4px;
  }

  &:not(:last-child) {
    margin-bottom: 4px;
  }

  & a {
    text-decoration: none;
  }

  @media (min-width: $extra-large) {
    .phone-list--header & {
      position: relative;

      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: -2px;
        height: 21px;
        background-position: center;
        background-repeat: no-repeat;
      }
    }

    .phone-list--header &--a1 {
      padding-left: 31px;

      &::before {
        width: 21px;
        background-image: url("../../img/ico-a1.svg");
      }
    }
    .phone-list--header &--all-operators {
      padding-left: 97px;

      &::before {
        width: 86px;
        background-image: url("../../img/ico-mobile-operators.svg");
      }
    }
  }
}
