.catalog-item {
  position: relative;
  width: calc(50% - 20px);
  margin-right: 10px;
  margin-bottom: 10px;
  margin-left: 10px;

  @media (min-width: $medium) {
    width: calc((100% / 3) - 20px);
  }

  @media (min-width: $extra-large) {
    margin-bottom: 20px;
  }

  & > a {
    display: block;

    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0) 81.13%, rgba(0, 0, 0, 0.588) 81.33%), linear-gradient(180deg, rgba(0, 0, 0, 0) 80.73%, rgba(0, 0, 0, 0.42) 98.88%);
    }
  }
}

.catalog-item__thumbnail {
  display: block;
  border-radius: 5px;
  width: 100%;
}

.catalog-item__title {
  position: absolute;
  top: 81.33%;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 5;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 4px;
  padding-bottom: 5px;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  color: #CCA643;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;

  @media (min-width: $extra-large) {
    font-size: 18px;
    line-height: 21px;
    color: #E8BE51;
    text-transform: uppercase;
  }

  .catalog-item:hover & {
    color: #ffffff;
    background: rgba(0, 0, 0, 0.9);
    box-shadow: inset 15px 15px 25px rgba(0, 0, 0, 0.5);
  }
}
