.contacts-page {
  padding-bottom: 30px;

  @media (min-width: $medium) {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }

  &.contacts-page--custom {
    @media (min-width: $extra-large) {
      max-width: 972px;
      margin-right: auto;
      margin-left: auto;
      padding-bottom: 40px;
    }
  }
}

.contacts-page__item {
  @media (min-width: $medium) {
    width: calc(50% - 10px);
  }

  &--content {
    margin-bottom: 28px;

    .contacts-page--custom & {
      @media (min-width: $extra-large) {
        width: calc(41% - 10px);
      }
    }

    & > p {
      margin-bottom: 20px;
      font-size: 18px;
      line-height: 18px;

      @media (min-width: $extra-large) {
        font-size: 21px;
        line-height: 21px;
      }
    }

    & a {
      color: inherit;
      text-decoration: none;
    }
  }

  &--form {
    .contacts-page--custom & {
      @media (min-width: $extra-large) {
        width: calc(59% - 10px);
      }
    }

    & > p {
      font-size: 18px;
      line-height: 21px;

      @media (min-width: $extra-large) {
        font-size: 21px;
        line-height: 25px;
      }
    }
  }
}
