.small-portfolio {
  @include List-Reset();
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  @media (min-width: $extra-large) {
    max-width: 285px;
    margin-right: -10px;
    margin-left: -10px;
  }
}

.small-portfolio__item {
  width: calc(100% / 3);
  max-width: 79px;
  margin-right: calc(15px / 2);
  margin-left: calc(15px / 2);
  margin-bottom: 15px;

  & img {
    display: block;
    border-radius: 5px;
  }
}
