.footer-menu {
  @include List-Reset();

  & > li {
    margin-bottom: 5px;
    font-size: 14px;
    line-height: 16px;

    @media (min-width: $extra-large) {
      margin-bottom: 6px;
      font-size: 13px;
      line-height: 15px;
    }
  }

  & a {
    color: inherit;
    text-decoration: none;
  }
}

