.portfolio {
  display: flex;
  flex-wrap: wrap;
  @include List-Reset();
  margin-right: -10px;
  margin-left: -10px;
}

.portfolio__wrapper {
  &--home {
    background: rgba(0, 0, 0, 0.5);
  }

  padding-top: 29px;
  padding-bottom: 29px;
  text-align: center;

  @media (min-width: $extra-large) {
    padding-top: 29px;
  }

  @media (min-width: $extra-large) {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

.portfolio__title {
  padding-top: 5px;
  padding-bottom: 3px;
  margin-bottom: 21px;
  font-size: 20px;
  line-height: 23px;

  @media (min-width: $medium) {
    font-size: 25px;
    line-height: 31px;
  }

  @media (min-width: $extra-large) {
    margin-bottom: 42px;
    font-size: 35px;
    line-height: 41px;
  }
}

.portfolio__more {
  margin-top: 19px;
}


