.error {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  align-content: flex-end;
  min-height: 100vh;
  padding: 14vh 11vw;
  box-sizing: border-box;
  background-color: #ffffff;
  background-image: url("../../img/404.jpg");
  background-size: cover;
  background-position: 28% 50%;
  background-repeat: no-repeat;
}

.error__title {
  width: 100%;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 45px;
  line-height: 53px;
  font-weight: 900;
  text-transform: uppercase;
}

.error__sub-title {
  width: 100%;
  margin-top: 30px;
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
}

.error__link {
  padding: 12px 27px;
  font-style: normal;
  font-size: 14px;
  line-height: 16px;
}
