.section-title {
  padding-top: 11px;
  padding-bottom: 6px;
  margin-bottom: 30px;
  margin-top: 0;
  @include Roboto(400, italic);
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  color: #ffffff;

  @media (min-width: $medium) {
    font-size: 25px;
    line-height: 31px;
  }

  @media (min-width: $extra-large) {
    padding-top: 5px;
    margin-bottom: 40px;
    font-size: 35px;
    line-height: 41px;
  }
}
