.main-menu {
  @include List-Reset(37px, 0, 0);
  text-align: center;

  @media (min-width: $medium) {
    display: flex;
    justify-content: space-between;
    margin-top: 0;
  }

  @media (min-width: $extra-large) {
    max-width: 940px;
    margin: 0 auto;
  }

  & a {
    display: inline-block;
    padding-top: 6px;
    padding-bottom: 5px;
    line-height: 19px;
    color: #ffffff;
    text-decoration: none;

    @media (min-width: $extra-large) {
      padding-top: 15px;
      padding-bottom: 14px;
    }

    &:hover {
      @extend %gradient-border;
    }
  }
}
