.single-product {
  padding: 29px 23px 23px 23px;
  margin-right: -15px;
  margin-left: -15px;
  background: rgba(0,0,0,.5);

  @media (min-width: $extra-large) {
    padding: 8px 20px 58px 40px;
    margin-right: -45px;
    margin-bottom: 100px;
    margin-left: 0;
    border-radius: 5px;
  }
}

.single-product__title {
  margin-top: 0;
  font-size: 25px;
  line-height: 31px;
  font-weight: 400;
  text-align: center;

  @media (min-width: $extra-large) {
    margin-bottom: 9px;
    font-size: 35px;
    line-height: 41px;
  }
}

.single-product__grid {
  margin-bottom: 35px;

  @media (min-width: $medium) {
    display: flex;
    justify-content: space-between;
  }
}

.single-product__detail {
  @media (min-width: $medium) {
    width: calc(50% - 10px);

    & > p {
      margin-top: 0;
    }
  }

  @media (min-width: $extra-large) {
    width: calc(40.7% - 10px);
    flex-grow: 1;
    font-size: 21px;
    line-height: 25px;

    & > p {
      margin-bottom: 12px;
    }
  }
}

.single-product__detail-title {
  font-weight: 700;
  color: #CCA643;
}

.single-product__parameters {
  width: 100%;
  margin-bottom: 40px;
  border-collapse: collapse;

  & tr td:last-child {
    color: #CCA643;
  }

  & td {
    @media (min-width: $extra-large) {
      padding-top: 2px;
      padding-bottom: 2px;
    }
  }
}

.single-product__description-title {
  @media (min-width: $extra-large) {
    padding-left: 23px;
    font-size: 21px;
    line-height: 25px;
  }
}

.single-product__description {
  @media (min-width: $extra-large) {
    border: 1px solid #fff;
    padding: 0 22px 22px 22px;
    margin-right: 20px;
    font-size: 21px;
    line-height: 25px;
  }

  & > p {
    @media (min-width: $extra-large) {
      margin-top: 14px;
      margin-bottom: 14px;
    }
  }
}

.single-product__order {
  padding-left: 30px;
  padding-right: 30px;
}
