.about {
  @media (min-width: $extra-large) {
    display: flex;
    align-items: center;
  }
}

.about__wrapper {
  padding-top: 27px;
  padding-bottom: 30px;

  @media (min-width: $extra-large) {
    padding-top: 39px;
    padding-bottom: 40px;
  }
}

.about__picture {
  display: none;

  @media (min-width: $extra-large) {
    display: block;
    flex-shrink: 0;
    width: 42%;
    margin-top: 40px;
  }
}

.about__title {
  padding-top: 5px;
  padding-bottom: 3px;
  margin-bottom: 22px;
  font-size: 20px;
  line-height: 23px;

  @media (min-width: $medium) {
    font-size: 25px;
    line-height: 31px;
  }

  @media (min-width: $extra-large) {
    display: inline-block;
    margin-bottom: 17px;
    font-size: 35px;
    line-height: 41px;
  }
}

.about__content {
  padding-left: 5px;
  padding-right: 5px;
  text-align: center;

  @media (min-width: $extra-large) {
    padding-right: 0;
    text-align: left;
  }

  & > p {
    font-size: 18px;
    line-height: 20px;
    color: #FFFFFF;
    text-align: left;

    @media (min-width: $extra-large) {
      font-size: 21px;
      line-height: 24px;
    }
  }
}

.about__more {
  margin-top: 11px;
  padding-right: 25px;
  padding-left: 25px;

  @media (min-width: $extra-large) {
    margin-top: 7px;
  }
}
