.slick-arrow {
  position: absolute;
  z-index: 10;
  top: calc(50% - 10px);
  display: flex;
  padding: 0;
  width: 40px;
  height: 20px;

  @media (min-width: $medium) {
    width: 80px;
    height: 40px;
    top: calc(50% - 20px);
  }

  @media (min-width: $extra-large) {
    width: 100px;
    height: 50px;
    top: calc(50% - 25px);
  }

  border: none;
  cursor: pointer;

  .promo--home & {
    & .arrow-icon {
      background: rgba(40, 40, 40, 0.5);
      width: 20px;
      height: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;

      @media (min-width: $medium) {
        width: 40px;
        height: 40px;
      }

      @media (min-width: $extra-large) {
        width: 50px;
        height: 50px;
      }
    }

    & .slick-thumb-nav {
      width: 100%;
      height: 100%;
      background-size: cover;
      background-position: center;
    }

    &.slick-next {
      flex-direction: row-reverse;
    }
  }

  &:hover {
    background: rgba(255, 255, 255, 0.8);

    & svg {
      fill: #9D9D9D;
    }
  }

  &.slick-prev {
    left: 15px;

    @media (min-width: $extra-large) {
      left: calc(((100% - 1170px) / 2) - 50px);
    }
  }

  &.slick-next {
    right: 15px;

    @media (min-width: $extra-large) {
      right: calc(((100% - 1170px) / 2) - 50px);
    }
  }

  & svg {
    fill: #ffffff;

    @media (min-width: $medium) {
      width: 10px;
      height: auto;
    }
  }
}
